:root {
  font-size: 62.5%;
}

*, :before, :after {
  box-sizing: border-box;
  font: inherit;
  margin: 0;
  padding: 0;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

body {
  min-height: 95vh;
  background-color: #fdfdfd;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  height: 10rem;
  width: 100%;
  opacity: .86;
  z-index: 100;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  display: flex;
  position: fixed;
  top: 0;
}

@media screen and (min-width: 501px) {
  .header {
    height: 6rem;
    flex-direction: row;
    padding-top: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

@media screen and (min-width: 640px) {
  .header {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.navbar ul {
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 500;
  list-style-type: none;
  display: flex;
}

.navbar a {
  color: #000;
  letter-spacing: 1px;
  border-radius: 2rem;
  margin: 0 1rem;
  padding: .4rem .9rem;
  text-decoration: none;
}

.navbar a:hover {
  color: #fdfdfd;
  background-color: #000;
  border-radius: 2rem;
  transition-duration: .5s;
}

.logo {
  height: 5rem;
  width: 10rem;
  background-image: url("logo05.6d9f5814.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

form {
  width: 90%;
  margin: 15rem auto 0;
}

@media screen and (min-width: 768px) {
  form {
    width: 70%;
  }
}

@media screen and (min-width: 940px) {
  form {
    width: 60%;
  }
}

@media screen and (min-width: 1200px) {
  form {
    width: 50%;
  }
}

label {
  letter-spacing: 1px;
  margin-top: 1.2rem;
  margin-bottom: .8rem;
  font-size: 1.6rem;
  display: block;
}

input[type="text"], input[type="email"], textarea {
  width: 100%;
  color: #484848;
  border: 1px solid #ccc;
  border-radius: .8rem;
  margin-bottom: 1em;
  padding: .5em;
  font-size: 1.6rem;
}

input[type="text"]:focus, input[type="email"]:focus, textarea:focus {
  outline: 1px solid #000;
}

#message {
  height: 15rem;
}

input[type="submit"] {
  cursor: pointer;
  background-color: #fdfdfd;
  border: 1px solid #000;
  border-radius: 2rem;
  margin-top: 1rem;
  padding: .5rem 1rem;
  font-size: 1.8rem;
}

input[type="submit"]:hover {
  color: #fff;
  background-color: #000;
}

